/*  2. Header top Area
/*--------------------------------*/

.topbar-left {
  background: #17aac9;
  width: 80%;
  position: relative;
  z-index: 1;
}

.topbar-left::after {
  content: "";
  position: absolute;
  right: -50px;
  top: 0;
  border-top: 48px solid transparent;
  border-bottom: 0 solid transparent;
  border-left: 50px solid #17aac9;
  z-index: 10;
}

.topbar-left::before {
  content: "";
  position: absolute;
  left: -110px;
  top: 0;
  background: #17aac9;
  width: 115px;
  height: 48px;
  z-index: -1;
}

.topbar-left ul li {
  display: inline-block;
}

.topbar-area {
  background: #11193b;
}

.topbar-left ul li a {
  color: #f5f5f5;
  display: block;
  margin-right: 20px;
  padding: 11px 0;
  text-decoration: none;
  font-size: 15px;
  letter-spacing: 0.6px;
}

.topbar-left ul li a i {
  color: #fff;
  font-size: 16px;
  padding-right: 3px;
}

.topbar-left ul li:last-child a {
  margin-right: 0px;
}

.topbar-right {
  float: right;
}

.topbar-right ul li {
  display: inline-block;
  position: relative;
}

.topbar-right ul li > ul > li {
  display: block;
}

.topbar-right > ul > li > a {
  color: #c4d5f6;
  display: block;
  padding: 12px 30px 10px 0px;
  position: relative;
  font-size: 15px;
}

.topbar-right ul li ul {
  background: #1b2654;
  left: 0;
  position: absolute;
  top: 100%;
  width: 130px;
  z-index: 9999999;
  padding: 10px 15px;
  opacity: 0;
  transform: scaleY(0);
  transform-origin: 0 0 0;
  transition: all 0.4s ease 0s;
  visibility: hidden;
}

.topbar-right > ul > li > ul a {
  color: #c4d5f6;
  font-size: 13px;
}

.topbar-right ul li:hover ul {
  visibility: visible;
  opacity: 1;
  transform: scaleY(1);
}

/* .topbar-right>ul>li>a::after {
  color: #c4d5f6;
