  font-size: 12px;
  position: absolute;
  right: 15px;
  top: 13px;
} */

.topbar-right > ul > li:last-child a::after {
  display: none;
}

.topbar-right > ul > li:last-child a {
  padding-right: 0px;
}

.topbar-right img {
  margin-right: 5px;
}

